.container {
  font-size: 14px;
  position: fixed;
  z-index: 10;
}

.top-middle {
  top: 1rem;
  left: 50%;
  transform: translateX(-25%);
  animation: toast-in-top 0.7s;
}

.bottom-right {
  bottom: 1rem;
  right: 1rem;
  animation: toast-in-right 0.7s;
}

.Warning {
  background-color: var(--color-scale-warning);
}

.Info {
  background-color: var(--color-scale-info);
}

.Success {
  background-color: var(--color-scale-success);
}

.Error {
  background-color: var(--color-scale-error);
}

.notification {
  margin-bottom: 1rem;
  border-radius: 4px;
  box-shadow: 0 0 10px #999;
  color: #000;
  opacity: 0.9;
  transition: 0.3s ease;
}

.notification:hover {
  box-shadow: 0 0 12px #fff;
  opacity: 1;
}

.toast {
  height: 50px;
  width: 365px;
  color: #fff;
  padding: 20px 15px 10px 10px;
}

.container button {
  float: right;
  background: none;
  border: none;
  color: #fff;
  opacity: 0.8;
  cursor: pointer;
}

.title {
  font-weight: 700;
  font-size: 16px;
  text-align: left;
  margin-top: 0;
  margin-bottom: 6px;
  width: 300px;
  height: 18px;
}

.description {
  margin: 0;
  text-align: left;
}

@keyframes toast-in-right {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0);
  }
}

@keyframes toast-in-top {
  from {
    transform: translate(-25%, -100%);
  }
  to {
    transform: translate(-25%, 0%);
  }
}
