.pizza_container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.pizza {
  position: relative;
  width: 400px;
  height: 400px;
  background-color: #ffeb3b;
  border-radius: 50%;
  border: 4px solid orange;
  box-shadow: 0 0 0px 10px #dd8126;
}

.pizza:before {
  content: "";
  position: absolute;
  width: 400px;
  height: 400px;
  background-color: var(--color-scale-white);
  transform: rotate(45deg);
  left: 70%;
  z-index: 20;
  animation: clock 3s infinite;
}

.pizza .basil {
  position: absolute;
  width: 400px;
  height: 400px;
  border-radius: 50%;
  overflow: hidden;
}

.pizza .basil .leaf {
  position: absolute;
  width: 25px;
  height: 25px;
  background-color: #4caf50;
  z-index: 13;
  border-radius: 100px 0;
  box-shadow: 50px 100px #4caf50, 150px 120px #4caf50, 130px 40px #4caf50,
    200px 250px #4caf50, 250px 300px #4caf50, 300px 100px #4caf50,
    350px 200px #4caf50, 35px 280px #4caf50, 150px 320px #4caf50,
    130px 40px #4caf50, 100px 200px #4caf50, 250px 60px #4caf50,
    230px 160px #4caf50;
}

.pizza .onions {
  position: absolute;
  width: 400px;
  height: 400px;
  border-radius: 50%;
  overflow: hidden;
  z-index: 12;
  background-image: radial-gradient(
      circle at 10px 0px,
      transparent 6px,
      #d2d2d2 5px,
      #f5f5f5 11px,
      transparent 13px
    ),
    radial-gradient(
      circle at 0px 10px,
      transparent 6px,
      #d2d2d2 5px,
      #f5f5f5 11px,
      transparent 13px
    );
  background-size: 80px 90px, 150px 150px;
}

.pizza .olives {
  position: absolute;
  width: 400px;
  height: 400px;
  border-radius: 50%;
  overflow: hidden;
  z-index: 11;
  transform: rotate(25deg);
  background-image: radial-gradient(
    circle at 70px 70px,
    #455a64 3px,
    #0f1315 9px,
    transparent 10px
  );
  background-size: 100px 100px;
}

.pizza .pepperonis {
  position: absolute;
  width: 400px;
  height: 400px;
  border-radius: 50%;
  overflow: hidden;
  transform: rotate(15deg);
  z-index: 12;
  background-image: radial-gradient(
    circle at 40px 40px,
    #ea5353 14px,
    #b04141 16px,
    transparent 18px
  );
  background-size: 80px 80px;
}

.pizza .slices {
  display: flex;
  position: relative;
  width: 100%;
  height: 100%;
  justify-content: center;
}

.pizza .slices .slice {
  width: 1px;
  height: 100%;
  background-color: #e8d83a;
}

.pizza .slices .slice_1 {
  transform: rotate(45deg);
}

.pizza .slices .slice_2 {
  transform: rotate(90deg);
}

.pizza .slices .slice_3 {
  transform: rotate(135deg);
}

.pizza .slices .slice_4 {
  transform: rotate(180deg);
}

@keyframes clock {
  23% {
    transform: rotate(45deg);
    visibility: hidden;
  }
  24% {
    transform: translateX(-280px) translateY(280px) rotate(45deg);
  }
  25% {
    transform: translateX(-280px) translateY(280px) rotate(45deg);
    visibility: visible;
  }
  48% {
    transform: translateX(-280px) translateY(280px) rotate(45deg);
    visibility: hidden;
  }
  49% {
    transform: translateX(-560px) rotate(45deg);
  }
  50% {
    transform: translateX(-560px) rotate(45deg);
    visibility: visible;
  }
  73% {
    transform: translateX(-560px) rotate(45deg);
    visibility: hidden;
  }
  74% {
    transform: translateX(-280px) translateY(-280px) rotate(45deg);
  }
  75% {
    transform: translateX(-280px) translateY(-280px) rotate(45deg);
    visibility: visible;
  }
  98% {
    transform: translateX(-280px) translateY(-280px) rotate(45deg);
    visibility: hidden;
  }
  99% {
    transform: rotate(45deg);
    visibility: visible;
  }
}
